                        :rules="required"
                        :error-messages="errors"
                        counter
                        multiple
                        required
                        prepend-icon="mdi-paperclip"
                    >
                        <template v-slot:selection="{ index, text }">
                            <v-chip
                                v-if="index < 2"
                                color="deep-purple accent-4"
                                dark
                                label
                                small
                            >
                                {{ text }}
                            </v-chip>

                            <span
                                v-else-if="index === 2 && files.length === 3"
                                class="text-overline grey--text text--darken-3 mx-2"
                            >
                                + 1 Arquivo
                            </span>

                            <span
                                v-else-if="index === 2"
                                class="text-overline grey--text text--darken-3 mx-2"
                            >
                                +{{ files.length - 2 }} Arquivos
                            </span>
                        </template>
                    </v-file-input>
                </validation-provider>

                <router-link 
                    to="/" 
