                >
                    <v-btn
                        color="error"
                        class="mx-3"
                    >
                        Cancelar
                    </v-btn>
                </router-link>
                <v-btn 
                    color="primary"
                    @click="step = 2"
                >
                    Continuar
                </v-btn>
            </validation-observer>
        </v-stepper-content>

        <v-stepper-step step="2">
            Informaes
            <small>Insira informaes do veculo</small>
        </v-stepper-step>
        <v-stepper-content step="2">
            <validation-observer
                ref="observer"
                v-slot="{ invalid }"
            >
                <form 
                    @submit="submit"
                    ref="form"
                >
                    <v-row 
                        class="ms-1 mb-4"
                    >
                        <validation-provider
                            v-slot="{ errors }"
                            name="comprimento"
                            rules="required"
                        >
                            <v-text-field
                            v-model="length"
                            :error-messages="errors"
                            label="Comprimento"
                            placeholder="em metros"
                            required
                            prepend-icon="mdi-ruler"
                            ></v-text-field>
                        </validation-provider>

                        <validation-provider
                            v-slot="{ errors }"
                            name="largura"
                            rules="required"
                        >
                            <v-text-field
                            v-model="width"
                            :error-messages="errors"
                            label="Largura"
                            placeholder="em metros"
                            required
                            prepend-icon="mdi-ruler"
                            ></v-text-field>
                        </validation-provider>

                        <validation-provider
                            v-slot="{ errors }"
                            name="altura"
                            rules="required"
                        >
                            <v-text-field
                            v-model="height"
                            :error-messages="errors"
                            label="Altura"
                            placeholder="em metros"
                            required
                            prepend-icon="mdi-ruler"
                            ></v-text-field>
                        </validation-provider>

                        <validation-provider
                            v-slot="{ errors }"
                            name="tara"
                            rules="required"
                        >
                            <v-text-field
                            v-model="weight"
                            :error-messages="errors"
                            label="Tara"
                            placeholder="em KG"
                            required
                            prepend-icon="mdi-scale"
                            ></v-text-field>
                        </validation-provider>
                    </v-row>

                    <div class="text-overline">
                        <v-icon>mdi-bus-marker</v-icon>
                        <span> Rota</span>
                    </div>

                    <v-row class="mb-2">
                        <v-col cols="2">
                            <v-select
                                v-model="statesOrigin"
                                :items="states"
                                label="UF Origem"
                                item-value="sigla"
                                item-text="sigla"
                                @click="getStates()"
                            />
                            <v-select
                                v-model="statesDestination"
                                :items="states"
                                label="UF Destino"
                                item-value="sigla"
                                item-text="sigla"
                                @click="getStates()"
                            />
                        </v-col>
                        <v-col cols="2">
                            <v-select
                                v-model="citiesOrigin"
                                :items="citiesOrigin"
                                label="Cidade Origem"
                                item-value="nome"
                                item-text="nome"
                                @click="getCitiesOrigin()"
                            />
                            <v-select
                                v-model="citiesDestination"
                                :items="citiesDestination"
                                label="Cidade Destino"
                                item-value="nome"
                                item-text="nome"
                                @click="getCitiesDestination()"
                            />
                        </v-col>
                    </v-row>

                    <v-row>
                        <v-col cols="5">
                            <validation-provider
                                v-slot="{ errors }"
                                name="type"
                                rules="required"
                            >
                                <v-select
                                    v-model="type"
                                    :items="truckTypes"
                                    :error-messages="errors"
                                    label="Tipo de Veclo"
                                    class="mr-1"
                                    prepend-icon="mdi-truck"
                                    item-value="name"
                                    item-text="name"
                                    @change="subtype = null; subtypeEspecific = null"
                                    required
                                />
                            </validation-provider>
                        
                            <validation-provider
                                v-if="type === 'CVC - Combinao Veicular de Carga'"
                                v-slot="{ errors }"
                                name="subtype"
                                rules="required"
                            >
                                <v-select
                                    v-model="subtype"
                                    :items="truckTypes[0].subtypes"
                                    :error-messages="errors"
                                    validate-on-blur
                                    label="Subtipo"
                                    class="mr-1"
                                    prepend-icon="mdi-truck"
                                    item-value="subtypes"
                                    item-text="subtypes"
                                    required
                                />
                            </validation-provider>
                            <validation-provider
                                v-if="type === 'Prancha'"
                                v-slot="{ errors }"
                                name="subtype"
                                rules="required"
                            >
                                <v-select
                                    v-model="subtype"
                                    :items="truckTypes[1].subtypes"
                                    :error-messages="errors"
                                    validate-on-blur
                                    label="Subtipo"
                                    class="mr-1"
                                    prepend-icon="mdi-truck"
                                    item-value="name"
                                    item-text="name"
                                    @change="subtypeEspecific = null"
                                    required
                                />
                            </validation-provider>
                            <validation-provider
                                v-if="type === 'Ba'"
                                v-slot="{ errors }"
                                name="subtype"
                                rules="required"   
                                required
                            >
                                <v-select
                                    v-model="subtype"
                                    :items="truckTypes[2].subtypes"
                                    :error-messages="errors"
                                    validate-on-blur
                                    label="Subtipo"
                                    class="mr-1"
                                    prepend-icon="mdi-truck"
                                    required
                                    item-value="subtypes"
                                    item-text="subtypes"
                                />
                            </validation-provider>
                            <validation-provider
                                v-if="type === 'Guindaste'"
                                v-slot="{ errors }"
                                name="subtype"
                                rules="required"
                            >
                                <v-select
                                    v-model="subtype"
                                    :items="truckTypes[3].subtypes"
                                    :error-messages="errors"
                                    validate-on-blur
                                    label="Subtipo"
                                    class="mr-1"
                                    prepend-icon="mdi-truck"
                                    required
                                    item-value="subtypes"
                                    item-text="subtypes"
                                />
                            </validation-provider>

                            <validation-provider
                                v-if="type === 'Prancha' && subtype === 'Anual'"
                                v-slot="{ errors }"
                                name="subtypeEspecific"
                                rules="required"   
                                required
                            >
                                <v-select
                                    v-model="subtypeEspecific"
                                    :items="truckTypes[1].subtypes[0].subtypes"
                                    :error-messages="errors"
                                    validate-on-blur
                                    label="Subtipo"
                                    class="mr-1"
                                    prepend-icon="mdi-truck"
                                    required
                                    item-value="subtypes"
                                    item-text="subtypes"
                                />
                            </validation-provider>
                        </v-col>

                        <v-col id="truckIcons">
                            <v-img
                                v-if="subtype === 'Bitrem 9 eixos'"
                                max-height="150"
                                max-width="300"
                                src="../assets/images/bitrem9.png"
                                class="mx-auto my-5"
                                contain
                            />
                            <v-img
                                v-if="subtype === 'Bitrem 7 eixos'"
                                max-height="150"
                                max-width="300"
                                src="../assets/images/bitrem7.png"
                                class="mx-auto my-5"
                                contain
                            />
                            <v-img
                                v-if="subtype === 'Rodotrem 9 eixos'"
                                max-height="150"
                                max-width="300"
                                src="../assets/images/rodotrem9.png"
                                class="mx-auto my-5"
                                contain
                            />
                            <v-img
                                v-if="subtype === 'Romeu e Julieta'"
                                max-height="150"
                                max-width="300"
                                src="../assets/images/romeuejulieta.png"
                                class="mx-auto my-5"
                                contain
                            />
                            <v-img
                                v-if="subtypeEspecific === 'Carga Indivisvel'"
                                max-height="150"
                                max-width="300"
                                src="../assets/images/indivisivel.png"
                                class="mx-auto my-5"
                                contain
                            />
                            <v-img
                                v-if="type === 'Ba' && subtype === 'Vazio ou Carregado' || subtype === 'Vazio'"
                                max-height="150"
                                max-width="300"
                                src="../assets/images/bau.png"
                                class="mx-auto my-5"
                                contain
                            />
                            <v-img
                                v-if="type === 'Prancha' && subtypeEspecific === 'Vazio'"
                                max-height="150"
                                max-width="300"
                                src="../assets/images/pranchavazio.png"
                                class="mx-auto my-5"
                                contain
                            />
                            <v-img
                                v-if="subtype === 'Superdimensionada e Indivisvel'"
                                max-height="150"
                                max-width="300"
                                src="../assets/images/superindivisivel.png"
                                class="mx-auto my-5"
                                contain
                            />
                            <v-img
                                v-if="type === 'Guindaste' && subtype === 'At 48 toneladas' || subtype === 'Acima de 45 toneladas'"
                                max-height="150"
                                max-width="300"
                                src="../assets/images/guindaste.png"
                                class="mx-auto my-5"
                                contain
                            />
                        </v-col>    
                    </v-row>

                    <router-link to="/" class="text-decoration-none">
                        <v-btn
                            color="error"
                        >
                            Cancelar
                        </v-btn>
                    </router-link>

                    <v-btn
                        @click="step = 1"
                        class="mx-3"
                        color="warning"
                        dark
                    >
                        Voltar
                    </v-btn>
                    <v-btn
                        color="primary"
                        class="mr-4"
                        type="submit"
                        :disabled="invalid"
                    >
                        Continuar
                    </v-btn>
                </form>
            </validation-observer>
        </v-stepper-content>
    </v-stepper>
