<!-- SCRIPT SCRIPT SCRIPT SCRIPT SCRIPT SCRIPT SCRIPT SCRIPT SCRIPT SCRIPT SCRIPT SCRIPT -->

<script>
import { required, regex } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'

setInteractionMode('eager')

extend('required', {
    ...required,
    message: 'Informe {_field_}',
})
extend('regex', {
    ...regex,
    message: '{_field_} {_value_} no cumpre o caso de regularidade {regex}',
})

export default {
    name: 'NewOrder',
    components: {
    ValidationProvider,
    ValidationObserver,
    },
    
    data: () => ({
        dialog: false,
        dialogDelete: false,
        menu: false,
        search: '',
        step: 1,
        select: null,
        type: '',
        statesDestination: '',
        statesOrigin: '',
        citiesDestination: '',
        citiesOrigin: '',
        states: [],
        cities: [],
        files: [],
        required: [ v => !!v || 'Este campo  obrigatrio'],

        truckTypes: [
            {
                id: 1,
                name: "CVC - Combinao Veicular de Carga",
                subtypes: ['Rodotrem 9 eixos', 'Bitrem 9 eixos', 'Romeu e Julieta', 'Bitrem 7 eixos']
            },
            {
                id: 2,
                name: "Prancha",
                subtypes: [
                    {
                        name: 'Anual',
                        subtypes: ['Carga Indivisvel', 'Vazio']
                    },
                    {
                        name: 'Superdimensionada e Indivisvel',
                        subtypes: "Indivisvel"
                    }
                ]
            },
            {
                id: 3,
                name: "Ba",
                subtypes:  ['Vazio ou Carregado', 'Vazio']
            },
            {
                id: 4,
                name: "Guindaste",
                subtypes: ['At 48 toneladas', 'Acima de 45 toneladas']
            },
        ],
    }),

    created() {

    },

    methods: {
        submit () {
            this.$refs.observer.validate()
        },
        getStates() {
            fetch(`https://servicodados.ibge.gov.br/api/v1/localidades/estados?orderBy=nome`)
            .then(r => r.json())
            .then((data) => {
                this.states = data;
            })
        },
        getCitiesOrigin() {
            fetch(`https://servicodados.ibge.gov.br/api/v1/localidades/estados/${this.statesOrigin}/municipios`)
            .then(r => r.json())
            .then((data) => {
                this.citiesOrigin = data;
            })
        },
        getCitiesDestination() {
            fetch(`https://servicodados.ibge.gov.br/api/v1/localidades/estados/${this.statesDestination}/municipios`)
            .then(r => r.json())
            .then((data) => {
                this.citiesDestination = data;
            })
        }
    },
}
</script>